body{
    min-height: 100vh;
    box-sizing: border-box;
    margin: 0;
    width: 100%;
    overflow-x: hidden;
    font-family: 'Poppins', sans-serif;
    background-color: rgb(172, 210, 241);
    
}



input:focus,
textarea:focus,
select:focus{
    outline: none;
}





.section-container{
    box-sizing: border-box;
    min-height: 100vh;
    position: relative;
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;
}
/* for small width screens */
@media only screen and (max-width:640px){
    .section-container{
        padding: 100px 0;

    }
}
