/* Position and size burger button */
.bm-burger-button{
    position: fixed;
    width: 36px;
    height: 36px;
    left: 20px;
    top: 32px;
   
}
/* color */
.bm-burger-bars{
    background: #373a47;
}
/* color to burger icons on hover */
.bm-burger-bars-hover{
    background: black;
}
/* size fo cross button */
.bm-cross-button{
    height: 24px;
    width: 24px;
}
.bm-cross{
    background: white;
}
/* ?sidebar wrapper styles */
.bm-menu-wrap{
    position: fixed;
    height: 100vh;
    overflow-x: hidden;
    box-shadow: 0px 4px 31px rgba(0, 0 , 0, 0.15);
}
.bm-menu{
    /* background-color: teal; */
    background: linear-gradient(292deg,#38b685 0.79%, #5c98d4 79.29%);
    padding: 2.5em 1.5em 0 0;
    height: 100vh;
    overflow: hidden !important;
}

.bm-item-list{
    padding: 0.8em;

}
.menu-item{
    position: relative;
    font-size: 2em;
    text-decoration: none;
    color: #373a47;
    font-family: Montserrat;
    font-weight: normal;
    font-style: normal;
    padding: 10px;
    text-decoration: none;
}
.menu-item::before{

    content: "";
    position: absolute;
    width: 100%;
    height: 0;
    bottom: 0;
    left: 0;
    background-color: #fff;
    visibility: hidden;
    transition: all 0.5s ease-in-out;
}
.active-item{
    font-weight: 500;
}
.menu-item:hover::before,
.active-item:before{
    visibility: visible;
    height: 3px;
}


@media only screen and (max-width:640px){
    .bm-burger-button{
        left: 0;
    }
}